.nav-container {
  box-sizing: border-box;
  position: fixed;
  background: transparent;
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  z-index: 10000;
  /* padding: 0 20px; */
  justify-content: space-between;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  /* margin-top: 5px; */
  padding: 50px 0;
  transition: 0.6s;
}

.nav-contents {
  box-sizing: content-box;
  position: fixed;
  background: transparent;
  justify-content: space-between;
  z-index: 100;
  display: flex;
  height: 60px;
  width: 100%;
  align-items: center;
  margin: auto;
}

.logo-image {
  width: 55px;
  height: 55px;
  /* position: relative; */
  margin-left: 2.5%;
  margin-right: 10%;
}

.nav-title {
  /* position: relative; */
  margin-left: 6%;
  color: white;
}

.nav-title > h2 > a {
  font-weight: 450;
  transition: 0.3s;
  cursor: pointer;
}

.nav-title > h2 > a:hover {
  color: blueviolet;
}

.ham-menu {
  display: none;
  color: white;
  width: 45px;
  height: 45px;
  font-size: 30px;
  font-weight: 400;
  outline: none;
  border: none;
  position: relative;
  z-index: 100;
  cursor: pointer;
}

.nav-list > ul {
  display: flex;
  position: relative;
  right: 10%;
  margin-right: 20px;
  transform-origin: top;
}

.nav-list > ul > a > li {
  /* background-color: gray; */
  cursor: pointer;
  letter-spacing: 1.5;
  margin-right: 5px;
  padding: 10px 15px;
  transition: 0.3s;
  text-transform: uppercase;
}

.nav-list > ul > a > li:hover {
  color: blueviolet;
}

@media screen and (max-width: 1024px) {
  .nav-list > ul > a > li {
    position: relative;
    /* width: 20%; */
  }

  .logo-image {
    width: 40px;
    height: 40px;
    margin-right: 10%;
    z-index: 101;
  }

  .nav-title {
    position: relative;
    font-size: 14px;
    left: 1%;
    z-index: 101;
  }
}

@media screen and (max-width: 768px) {
  .nav-container {
    background-color: black;
    margin-top: 0;
  }

  .ham-menu {
    display: block;
    position: relative;
    float: right;
    background: transparent;
  }

  .nav-list > ul:first-child {
    border-top: grey 1px solid;
  }

  .nav-list > ul {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    height: 290px;
    transform: scaleY(0);
    transform-origin: top;
    flex-direction: column;
    align-items: center;
    padding: 0;
    margin: 0;
    transition: 0.4s;
    z-index: 50;
    background-color: black;
  }

  .nav-list > ul.open {
    transform: scaleY(1);
  }

  .nav-title {
    margin-left: 15%;
  }

  .nav-list > ul > a > li {
    box-sizing: content-box;
    position: relative;
    top: 0px;
    margin: 0;
    padding: 15px;
    margin-bottom: 3px;
    width: 100vw;
    text-align: center;
    font-size: 20px;
  }
}

.scroll {
  background-color: black;
