          animation-delay: 1s;
}

@-webkit-keyframes fadeup {
  from{
    opacity: 0.2;
    transform: translateY(100%);
  }
  to{
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeup {
  from{
    opacity: 0.2;
    transform: translateY(100%);
  }
  to{
    opacity: 1;
    transform: translateY(0);
  }
}

