    }
}

@media screen and (max-width: 490px) {
  .contact {
    width: 100%;
    margin: 0 auto;
  }

  .contact > p {
    width: 350px;
}
.contact {
    min-height: 500px;
}
}

.footer {
  min-height: 200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #222;
}

.foot-imgs {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-top: 50px;
}

#bad {
  width: 38px;
  height: 38px;
  padding: 3px;
}

#verybad {
  width: 38px;
  height: 38px;
  padding: 3px;
}
#vbad {
  width: 40px;
  height: 40px;
  padding: 2px;
}

.footer .foot-imgs a img {
  width: 45px;
  height: 45px;
  padding: 0;
  margin: 0;
  object-fit: contain;
  border-radius: 10%;
  background-color: rgba(255, 255, 255, 0.8);
  margin-left: 5px;
  transition: 0.6;
}

.footer .foot-imgs a img:hover {
  transform: scale(1.1);
}

.copy-right blockquote {
  width: 100%;
  text-align: center;
  margin: 50px auto;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 300;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.footer .foot-imgs a:first-child :hover {
  background-color: #4267b2;
}

.footer .foot-imgs a:nth-child(2) :hover {
  background-color: #1da1f2;
}

.footer .foot-imgs a:nth-child(3) :hover {
  background-color: #e1306c;
}

.footer .foot-imgs a:nth-child(4) :hover {
  background-color: #1db954;
}

.footer .foot-imgs a:nth-child(5) :hover {
  background-color: white;
}

.footer .foot-imgs a:nth-child(6) :hover {
  background-color: #6e5494;
}

.footer .foot-imgs a:nth-child(7) :hover {
  background-color: #0072b1;
}

@media screen and (max-width: 490px) {
  .footer{
    width: 100%;
    margin: 0 auto;
  }
